<template>
  <header class="position-relative bg-white">
    <section class="mt-2 d-none d-sm-flex flex-column mb-3 container">
      <div class="ml-auto">
        <input class="px-2" type="text" placeholder="SEARCH">
      </div>
      <div class="row d-flex">
        <router-link class="nav-logo col-2 text-primary h1 mb-0" to="/">Lativ</router-link>
        <div class="col-10 d-flex align-items-end">
          <ul class="d-inline-flex align-items-center mb-0">
            <li class="px-2">
              <router-link
                class="nav_link px-2"
                :to="{ name: 'cardList', params: { gender: 'women',category:'all'}}"
                :class="{'nav_link_select' : $route.params.gender === 'women'}"
              >WOMEN</router-link>
            </li>
            <div class="py-1 border-right"></div>
          </ul>
          <ul class="d-inline-flex align-items-end mb-0 ml-auto">
            <li class="ml-auto d-flex text-primary">
              <a href class="line-r mx-2 h6">訂閱電子報</a>
              <a @click.prevent="routerToUser" href class="mx-2 h6">
                <span v-if="user_email">會員專區</span>
                <span v-else>登入/註冊</span>
              </a>
              <a class="mx-2 h6" v-if="user_email" @click.prevent="loginOut">登出</a>
              <span
                href
                class="ml-2 position-relative h6"
                @mouseenter="isHoverCart = true"
                @mouseleave="isHoverCart = false"
              >
                {{allQty}}個商品
                <div v-if="isAddToCart" class="add-cart-show position-absolute border">
                  <h6 class="text-center text-primary my-3">本商品已經加入購物車</h6>
                  <div class="border-top border-bottom w-75 mb-3 py-3 mx-auto">
                    <img :src="lastCartProduct.imgUrl" class="d-inline-block w-25" alt>
                    <h6 class="d-inline-block w-75 pl-1">{{showCartProduct(cart.length-1)}}</h6>
                  </div>
                  <div class="d-flex mb-3 mr-3">
                    <router-link
                      to="/check"
                      class="check-btn ml-auto bg-primary text-white px-3 py-1 rounded"
                    >前往結帳</router-link>
                  </div>
                </div>
                <div v-if="isHoverCart" class="add-cart-show position-absolute border">
                  <div v-if="cart.length !== 0">
                    <table class="text-center w-75 py-3 mx-auto">
                      <tr class="border-bottom">
                        <th>商品名稱</th>
                        <th>顏色</th>
                        <th>尺寸</th>
                        <th>數量</th>
                      </tr>
                      <tr v-for="(item,index) in cart" :key="index">
                        <td>{{item.title}}</td>
                        <td>{{item.colorObject.colorName}}</td>
                        <td>{{item.sizeObject.sizeName}}</td>
                        <td>{{item.qty}}</td>
                      </tr>
                    </table>
                    <div class="d-flex mb-3 mr-3">
                      <router-link
                        to="/check"
                        class="check-btn ml-auto bg-primary text-white px-3 py-1 rounded"
                      >前往結帳</router-link>
                    </div>
                  </div>
                  <div v-else class="text-center py-5">目前你還沒選購商品喔</div>
                </div>
              </span>
            </li>
          </ul>
        </div>
      </div>
    </section>
    <section class="mobile-header d-block d-sm-none position-fixed bg-white w-100 p-2">
      <div class="d-flex w-100">
        <div class="text-white bg-primary px-1 py-1 flex-center">Lativ</div>
        <div class="rounded d-flex mx-3 flex-grow-1">
          <i class="nav-search px-3 fas fa-search flex-center"></i>
          <input
            class="nav-search flex-grow-1 mr-2 border-0"
            type="text"
            name
            id
            placeholder="輸入關鍵字"
          >
        </div>
        <div class="px-1 py-1 position-relative h6">
          <i class="far fa-comment"></i>
          消息
        </div>
      </div>
      <ul class="d-flex text-unclick mb-0 mt-2">
        <li class="px-2">首頁</li>
        <li class="px-2">女裝</li>
      </ul>
    </section>
  </header>
</template>
<script>
import AxiosClass from "@/config/axiosClass.js";
export default {
  data() {
    return {
      selectedNav: "",
      isHoverCart: false,
      user_email: ""
    };
  },
  computed: {
    allQty() {
      return this.$store.state.cart.length;
    },
    isAddToCart() {
      return this.$store.state.isAddToCart;
    },
    lastCartProduct() {
      return this.$store.state.cart[this.$store.state.cart.length - 1];
    },
    cart() {
      return this.$store.state.cart;
    }
  },
  methods: {
    routerToUser() {
      if (this.user_email === "admin@gmail.com") {
        this.$router.push("/admin");
      } else if (this.user_email) {
        this.$router.push("/user");
      } else {
        this.$router.push("/login");
      }
    },
    showCartProduct(index) {
      return `${this.cart[index].title}-${this.cart[index].gender} ${
        this.cart[index].colorObject.colorName
      }-${this.cart[index].sizeObject.sizeName} NT$${
        this.cart[index].totalPrice
      }`;
    },
    loginOut() {
      const self = this;
      const loginOut = new AxiosClass("login_out", "get", {}, true);
      loginOut.doAxios(self, function(res) {
        self.checkCookie();
      });
    },
    checkCookie() {
      var cookieValue = document.cookie.replace(
        /(?:(?:^|.*;\s*)user_email\s*\=\s*([^;]*).*$)|^.*$/,
        "$1"
      );
      cookieValue = decodeURIComponent(cookieValue);
      if (cookieValue) {
        this.user_email = cookieValue;
      } else {
        this.user_email = "";
      }
    }
  },
  created() {
    this.checkCookie();
  },
  watch: {
    $route() {
      this.checkCookie();
    }
  }
};
</script>
<style lang="scss" scoped>
.nav-logo {
  text-decoration: none;
}
.add-cart-show {
  right: 0;
  width: 400px;
  z-index: 20;
  background-color: #fff;
}

tr,
th,
td {
  font-size: 12px;
  padding-top: 6px;
  padding-bottom: 6px;
}

.check-btn {
  font-size: 12px;
}

.mobile-header {
  top: 0;
  z-index: 100;
  right: 0;
}

.nav_link {
  font-size: 16px;
  color: #858585;
  text-decoration: none;
  &:hover {
    color: #4d3126;
  }
}

.fa-comment {
  height: 12px;
}

.nav-search {
  background-color: #eeeeee;
}
.fa-search {
  color: #c6c6c6;
  font-size: 20px;
}
.nav_link_select {
  color: #5d3b24;
  background-color: #f0ede5;
}
header {
  height: 105px;
  @media (min-width: 410px) and (max-width: 575px) {
    height: 80px;
  }
  @media (min-width: 575.98px) {
    height: auto;
  }
}
</style>
