import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cart: [],
    isAddToCart: false,
    asideData: {
      activity: {
      },
      women: [
        {
          title: "上身類",
          url: "tshirt",
          subCategory: [
            {
              name: "印花短T",
              url: "g-st"
            },
          ]
        },
      ],
    },
    checkStep: 0,
    totalPrice: 0,
    receiverData: {
      name: "",
      phone: "",
      address: "",
      invoice: "",
      invoiceData: ""
    },
    payType: '',
    isLoading: false,
    user_email: '',
  },
  mutations: {
    ADDTOCART(state, data) {
      state.cart.push(data);
      state.isAddToCart = true;
      setTimeout(function() {
        state.isAddToCart = false
      },1000)
    },
    CHECKSTEP(state, stepNum){
      state.checkStep = stepNum
    },
    CHECKCART(state,data){
      state.cart = data
    },
    TOTALPRICE(state,num){
      state.totalPrice = num
    },
    SET_RECEIVE(state,data){
      state.receiverData = data
    },
    SETPAYTYPE(state, data){
      state.payType = data
    },
    LOADING(state,boolean){
      state.isLoading = boolean
    },
    SET_USER_EMAIL(state,data){
      state.user_email = data
    },
    SET_receiverData(state,data){
      state.receiverData = data
    },
  },
  actions: {},
});
